* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
header {
  font-size: 30px;
  align-items: center;
  padding: 40px;
}
body {
  font-family: "lato", sans-seriff;
  line-height: 1.5;
  background: #f3f3f3;
}
button {
  background-color: #c72727;
  color: white;
  font-size: 13px;
  width: 80%;
  border: none;
  height: 40px;
  border-radius: 4px;
  cursor: pointer;
}
.header {
  margin: auto;
  padding: 0 2rem;
}
#home_container {
  max-width: 1100px;
  margin: auto;
  padding: 0 2rem;
  overflow: hidden;
}

.container {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
}

.container .search_box {
  background-color: white;
  padding: 20px;
  margin: 1rem;
}

.container .movies {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  align-items: center;
}
.img {
  background: url("https://i.ytimg.com/vi/z0pPhTLvzu4/hqdefault.jpg") no-repeat
    center center/cover;
  position: absolute;
  top: 10;
  left: 0;
  width: 100%;
  height: 100vh;
}

.card .movie {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}
.card .movie .movie_title {
  background-color: red;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
}
input[type="search"] {
  display: block;
  padding: 20px;
  font-size: 20px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 10px;
  margin-bottom: 10px;
  z-index: 1;
}
.card {
  display: grid;
  grid-template-rows: repeat(1, 1fr);
  background-color: white;
  margin: 2rem;
  padding: 20px;
  justify-self: start;
}
.not_valid {
  cursor: not-allowed;
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}
